import Vue from 'vue'
import App from './App.vue'
import router from './router'

import 'materialize-css/dist/css/materialize.min.css';

import VCalendar from 'v-calendar';

Vue.config.productionTip = false


/*  add calendar */
Vue.use(VCalendar, {
  componentPrefix: 'vc',
});



new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
